import "bootstrap/dist/css/bootstrap.min.css";
import "react-toastify/dist/ReactToastify.css";
import './App.css';

import SocialMedias from './Components/SocialMedias';
import { Network, Alchemy } from "alchemy-sdk";
import { useState } from 'react';
import NftList from './Components/NftList';
import NFTSales from './Components/NFTSales';
import Hero from './Components/Hero';
import {ToastContainer, toast} from "react-toastify";

import meta from "./assets/metamask.png";
//import ICOContract from './artifacts/contracts/ICO.sol/ICO.json';
import Button from "react-bootstrap/Button";
import { compactAddress } from "./helpers/utils";

function App() {

  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState({});
  const [provider, setProvider] = useState();
  const [contract, setContract] = useState();
  const [signer, setSigner] = useState();
  const contractAddress = '0x34D4d2E698Abd91c1Ab4ab62744673D2cb5EB7Ea';

  const [chain, setChain] = useState('Ethereum');
  const [addressToFind, setAddressToFind] = useState('');
  const [collectionContractToFind, setCollectionContractToFind] = useState('');
  const [amountNFTs, setAmountNFTs] = useState(0)
  const [listNFTs, setListNFTs] = useState([])
  const [listNftsOfCollection, setListNftsOfCollection] = useState([])
  const [listSpamNFTs, setListSpamNFTs] = useState([])
  const [tool, setTool] = useState('NFTList')
  const [isSpamContract, setIsSpamContract] = useState('');
  const [responseIsSpamContract, setResponseIsSpamContract] = useState('');
  const [contractReportSpam, setContractReportSpam] = useState('');
  const [contractAddressAirdrop, setContractAddressAirdrop] = useState('');
  const [tokenIsAirdrop, setTokenIsAirdrop] = useState('');
  const [responseIsAirdrop, setResponseIsAirdrop] = useState('');
 
  const settings = {
    apiKey: "8Srm8dlSQID3Dtw2xVb74bTD-Vw3_L51" //demo
  };

  if (chain == "Ethereum") {
    settings.network = Network.ETH_MAINNET
  }else if (chain == "Matic") {
    settings.network = Network.MATIC_MAINNET
  }else if (chain == "Optimism") {
    settings.network = Network.OPT_MAINNET
  }

  const alchemy = new Alchemy(settings);

  async function handleConnectWallet (){
    try {
      setLoading(true);
      
      let userAcc = await provider.send('eth_requestAccounts', []);
      setUser({account: userAcc[0], connected: true});
      
      // const contrSig = new ethers.Contract(contractAddress, ICOContract.abi, provider.getSigner())
      // setSigner( contrSig)
    } catch (error) {
      console.log(error);
      if (error.message == 'provider is undefined'){
        toastMessage('No provider detected.')
      } else if(error.code === -32002){
        toastMessage('Check your metamask')
      }
    } finally{
      setLoading(false);
    }
  }

  function isConnected(){
    if (!user.connected){
      toastMessage('You are not connected!')
      return false;
    }
    return true;
  }

  async function isGoerliTestnet(){
    const goerliChainId = "0x5";
    const respChain = await getChain();
    return goerliChainId == respChain;
  }

  async function getChain() {
    const currentChainId = await  window.ethereum.request({method: 'eth_chainId'})
    return currentChainId;
  }


  async function handleDisconnect(){
    try {
      setUser({});
      setSigner(null);
    } catch (error) {
      toastMessage(error.reason)
    }
  }

  function toastMessage(text) {
    toast.info(text)  ;
  }

  async function executeSigner(func, successMessage){
    try {
      if (!isConnected()) {
        return;
      }
      if (!await isGoerliTestnet()){
        toastMessage('Change to goerli testnet.')
        return;
      }
      setLoading(true);
      const resp = await func;  
      toastMessage("Please wait.")
      await resp.wait();
      toastMessage(successMessage)
    } catch (error) {
      console.log(error);
      toastMessage(error.reason)      
    } finally{
      setLoading(false);
    }
  }

  async function getAmountAndListNFTs(){
    cleanLists();
    try{
      const nftsForOwner = await alchemy.nft.getNftsForOwner(addressToFind);
      setAmountNFTs( nftsForOwner.totalCount);
    } catch (error) {
      toastMessage(error.message)
    }
  }

  async function getListNFTs(){
    cleanLists()
    try{
      const nftsForOwner = await alchemy.nft.getNftsForOwner(addressToFind);
      setListNFTs(nftsForOwner.ownedNfts);
    } catch (error) {
      //toastMessage(error.message)
    }
  }

  async function getCollectionContract() {
    cleanLists()
    try{
      const response = await alchemy.nft.getNftsForContract(collectionContractToFind, false);
      console.log(response.nfts);
      setListNftsOfCollection(response.nfts);
    } catch (error) {
      toastMessage(error.message)
    }

    //getContractMetadata()
  }

  async function getContractMetadata(){
    cleanLists()
    const contractMetadata = await alchemy.nft.getContractMetadata(collectionContractToFind);
    console.log(contractMetadata);
  }

  async function getIsSpamContract(){
    cleanLists()
    try{
      let response = await alchemy.nft
        .isSpamContract(isSpamContract);
      if (response){
        toastMessage('This contract is spam');
      }else{
        toastMessage('This contract is not reported as a spam');
      }
    }catch (error) {
      toastMessage(error.message)
    }
  }

  function returnChosedChain() {
    let chosedChain;
    if (chain === "Ethereum"){
      chosedChain = "eth-mainnet"
    }else{
      chosedChain = "matic-mainnet"
    }
    return chosedChain;
  }

  async function handleIsAirdrop(){
    cleanLists()
    const options = {method: 'GET', headers: {accept: 'application/json'}};

    fetch(`https://${returnChosedChain()}.g.alchemy.com/nft/v2/${settings.apiKey}/isAirdrop?contractAddress=${contractAddressAirdrop}&tokenId=${tokenIsAirdrop}`, options)
      .then(response => response.json())
      .then(response => {
        if (response){
          toastMessage('This nft is from airdrop');
        }else{
          toastMessage('This nft is not from airdrop');
        }
      })
      .catch(err => toastMessage(err.message));
    }

  async function handleReportSpam() {
    cleanLists()
    const options = {method: 'GET', headers: {accept: 'application/json'}};
    fetch(`https://${returnChosedChain()}.g.alchemy.com/nft/v2/${settings.apiKey}/reportSpam?address=${contractReportSpam}`, options)
      .then(response => response.json())
      .then(response => (response))
      .catch(err => toastMessage(err.message));
      //exemplo de spam 0x0ab9845aaf7b02654e38322e84cd3bc49a8edb89
  }

  function getDataAccount() {
      cleanLists()
      getAmountAndListNFTs();
      getListNFTs();  
  }


  function changeChain(e) {
    setChain(e.target.value)
  }

  function changeTool(e) {
    setTool(e.target.value)
    setAmountNFTs(0)
    setAddressToFind('')
    setCollectionContractToFind('')
    setIsSpamContract('');
    setContractReportSpam('')
    setResponseIsSpamContract('')
    setTokenIsAirdrop('')
    setContractAddressAirdrop('')
    setResponseIsAirdrop('')
    cleanLists();
  }

  function cleanLists(){
    setAmountNFTs(0)
    setListNFTs([])
    setListSpamNFTs([])
    setListNftsOfCollection([])
  }
  

  return (
    <div className="App">
      <ToastContainer position="top-center" autoClose={5000}/>
      <SocialMedias/>
      <Hero/>
      <section className='box-container'>

        {loading && 
          <h1>Loading....</h1>
        }

        { !user.connected ?<>
            <Button className="commands " variant="btn button" onClick={handleConnectWallet}>
              <img src={meta} alt="metamask" width="30px" height="30px"/>Connect to Metamask
            </Button>
            </>
          : <>
            <label>Welcome {compactAddress(user.account)}</label>
            <button className="button" style={{}} onClick={handleDisconnect}>Disconnect</button>
          </>
        }


        <h2 style={{marginTop: '30px', marginBottom: '30px'}}>
          Welcome to -__
        </h2>
        <h4 className="sub-title">Select the tool and chain</h4>
        <div className='grid-tools'>
          <div>
            <p>Select tool:</p>
          </div>
          <div>
            <select className='select-combo' value={tool} onChange={(e) => changeTool(e)}>
              <option value="NFTList">NFT List</option>
              <option value="Collection">Collection</option>
              <option value="IsSpamContract">Spam detect</option>
              <option value="ReportSpam">Report Spam</option>
              <option value="IsAirdrop">Is airdrop?</option>
              <option value="NFTSales">NFT Sales</option>
            </select>
          </div>
          <div>
            <p>
              Select chain:
            </p>
          </div>
          <div>
            <select className='select-combo' value={chain} onChange={(e) => changeChain(e)}>
              <option value="Ethereum">Ethereum</option>
              <option value="Matic">Matic</option>
              <option value="Optimism">Optimism</option>
            </select>
          </div>
        </div>
      </section>
      <h4 className="sub-title">Filters</h4>
      {/* a partir daqui tenho que trocar a ferramenta */}
      {/* primeira ferramenta é a lista de nfts */}
      { tool === "NFTList" &&
        <div className='box-container'>
          <div className='grid-filter'>
            <label>Account address (EOA):</label>
            <input type="text" onChange={(e) => setAddressToFind(e.target.value)} value={addressToFind}/>
          </div>


          <button className='button' onClick={getDataAccount}>Investigate</button>
          {amountNFTs && 
            <NftList title={"List of NFTs"} amountNFTs={amountNFTs} listNFTs={listNFTs} settings={settings} chain={chain}/>
          }
        </div>
      }
      {/* segunda ferramenta é os verificar se o contrato é spamcontratos spam */}
      { tool === "Collection" &&
        <div className='box-container'>
          <div className='grid-filter'>
            <label>Collection contract:</label>
            <input type="text" onChange={(e) => setCollectionContractToFind(e.target.value)} value={collectionContractToFind}/>
          </div>  

        <button className='button' onClick={getCollectionContract}>Investigate</button>
        <NftList title="Collection" amountNFTs={listNftsOfCollection.length} listNFTs={listNftsOfCollection} chain={chain} settings={settings}/>      
      </div>
      }
      {/* terceira ferramenta é a lista de nfts por contrato */}
      { tool === "IsSpamContract" && <div className='box-container'>
          <div className='grid-filter'>  
            <label>Collection contract:</label>
            <input type="text" onChange={(e) => setIsSpamContract(e.target.value)} value={isSpamContract}/>
          </div>
        <button className='button' onClick={getIsSpamContract}>Check</button>
        <label>{responseIsSpamContract}</label>
      </div>
      }

      {/* quarta ferramenta Report spam */}
      { tool === "ReportSpam" && <div className='box-container'>
          <div className='grid-filter'>
            <label>Contract to report:</label>
            <input type="text" onChange={(e) => setContractReportSpam(e.target.value)} value={contractReportSpam}/>
          </div>
          <button className='button' onClick={handleReportSpam}>Report</button>
      </div>
      }

      {/* quinta ferramenta IsAirdrop */}
        { tool === "IsAirdrop" && <div className='box-container'>
          <div className='grid-filter'>
            <label>NFT Contract:</label>
            <input type="text" onChange={(e) => setContractAddressAirdrop(e.target.value)} value={contractAddressAirdrop}/>
            <label>Token Id:</label>
            <input type="number" onChange={(e) => setTokenIsAirdrop(e.target.value)} value={tokenIsAirdrop}/>
          </div>
        <button className='button' onClick={handleIsAirdrop}>Check</button>
        <label>{responseIsAirdrop}</label>
      </div>
      }
      {/* sexta ferramenta IsAirdrop */}
      { tool === "NFTSales" && <>
        <NFTSales chain={chain} settings={settings}/>
      </>
      }
      <SocialMedias/>

    </div>
  );
}

export default App;

